.footer-page {
  padding: 2rem;
  width: 100%;
}

.footer-container {
  /* box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px,
    rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px; */
  /* border: 1px dashed #0f089f; */
}

.footer-container-grid {
  display: grid;
  grid-template-columns: auto auto auto auto;
  gap: 2rem;
}

.footer-container-grid-card:nth-child(1) .footer-logo {
  max-width: 150px;
  max-height: 150px;
  margin: auto;
}

.footer-container-grid-card:nth-child(1) {
  display: flex;
  /* justify-content: center; */
  align-items: center;
}

.footer-logo img {
  width: 100%;
  height: 100%;
}

.footer-nav-links {
  list-style-type: none;
  text-decoration: none;
  color: black;
}

.footer-nav-links:hover {
  background: linear-gradient(150deg, #0f089f, #e6166a);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.footer-container-grid-card:nth-child(2) p {
  max-width: 300px;
}
.footer-container-grid-card:nth-child(2) {
  display: flex;
  justify-content: center;
}
.footer-container-grid-card:nth-child(2) div {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer-container-grid-card:nth-child(3) {
  /* display: flex;
  justify-content: center; */
  align-items: center;
}
.footer-container-grid-card:nth-child(3) div {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
}
.footer-container-grid-card:nth-child(4) {
  display: flex;
  align-items: baseline;
}
.footer-container-grid-card:nth-child(4) div {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
}


.footer-social-media a img {
  /* clip-path: circle(); */
  background-color: white;
  transform: scale(0.7);
}

.footer-social-media a {
  text-decoration: none;
}

.footer-container-grid-card p {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer-social-media {
  cursor: pointer;
}

/* .footer-social-media a:nth-child(1):hover img {
  transform: scale(1.8);
}
.footer-social-media a:nth-child(2):hover img {
  transform: scale(1.4);
} */



@media (max-width: 1000px) {
  .footer-container-grid {
    grid-template-columns: auto auto;
  }
}

@media (max-width: 768px) {
  .footer-page {
    padding: 1rem;
    width: 100%;
  }
  .footer-container-grid {
    grid-template-columns: auto;
    gap: 0rem;
  }

  .footer-container-grid-card:nth-child(1),
  .footer-container-grid-card:nth-child(2),
  .footer-container-grid-card:nth-child(3) {
    justify-content: flex-start;
  }

  .footer-container-grid-card:nth-child(1) .footer-logo {
    max-width: 100px;
    max-height: 100px;
    display: flex;
    justify-content: baseline;
    margin-left:0 ;
    margin-bottom: 0;
  }
}

